*{box-sizing:border-box}
body{margin:0;padding:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;background-color:#eff3f7}
html,body,#root,.container{display:flex;justify-content:center;height:100%}
.loader{width:100%;height:100%;font-weight:600;display:flex;justify-content:center;align-items:center}
.movies{display:flex;width:80%;align-items:flex-start;justify-content:space-between;flex-wrap:wrap;padding:70px 50px 50px;font-weight:300}
.movies .movie{display:flex;padding:20px;font-size:14px;color:#adaeb9;align-items:flex-start;justify-content:space-between;width:45%;margin-bottom:70px;background-color:#fff;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025);border-radius:5px}
.movie img{position:relative;top:-50px;width:100%;max-width:150px;margin-right:30px;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),0 8px 16px -8px rgba(0,0,0,0.3),0 -6px 16px -6px rgba(0,0,0,0.025)}
.movie .movie__title{display:block;padding-bottom:5px;font-weight:300;font-size:24px;color:#2c2c2c}
.movie .movie__year{display:block;padding:0;color:#b2b3bc}
.movie .movie__genres{padding:0;list-style:none;padding:0;margin:0;display:flex;margin:5px 0}
.movie__genres li{margin-right:10px;font-size:14px}